module.exports = function insertNode(node, parent, key, version, optimizedPath) {
    node[_key] = key;
    node[_parent] = parent;

    if (version !== undefined) {
        node[_version] = version;
    }
    if (!node[_abs_path]) {
        node[_abs_path] = optimizedPath.slice(0, optimizedPath.index).concat(key);
    }

    parent[key] = node;

    return node;
};



