module.exports = function createHardlink(from, to) {

    // create a back reference
    var backRefs = to[_refs_length] || 0;
    to[_ref + backRefs] = from;
    to[_refs_length] = backRefs + 1;

    // create a hard reference
    from[_ref_index] = backRefs;
    from[_context] = to;
};



